<template>
    <div id="app" ref='app' style="height:100%; width:100%">
        <div id="kline" ref='kline' style="height:400px; width:500px"></div>  

        <div>
            <button class="1D" @click="ChangePeriod(0)">1D</button>
            <button class="1W" @click="ChangePeriod(1)">1W</button>
            <button class="1Min" @click="ChangePeriod(4)">1Min</button>
            <button class="3Min" @click="ChangePeriod(20003)">3Min</button>
            <button class="5Min" @click="ChangePeriod(5)">5Min</button>
            <button class="15Min"  @click="ChangePeriod(6)">15Min</button>
            <button class="30Min" @click="ChangePeriod(7)">30Min</button>
            <button class="60Min" @click="ChangePeriod(8)">60Min</button>
            <button class="2H" @click="ChangePeriod(20120)">2H</button>
            <button class="4H" @click="ChangePeriod(20240)">4H</button>
            <button class="6H" @click="ChangePeriod(20360)" >6H</button>
            <button class="12H" @click="ChangePeriod(20720)">12H</button>
        </div>

        <div>
            <button class="MACD" @click="ChangeIndex(1,'MACD')" >MACD</button>
            <button class="KDJ" @click="ChangeIndex(1,'KDJ')">KDJ</button>
            <button class="BIAS" @click="ChangeIndex(2,'BIAS')">BIAS</button>
            <button class="BOLL" @click="ChangeIndex(0,'BOLL')">BOLL</button>
            <button class="BBI" @click="ChangeIndex(0,'BBI')">BBI</button>
            <button class="RSI" @click="ChangeIndex(2,'RSI')">RSI</button>
            <button class="MA" @click="ChangeIndex(0,'MA')">MA</button>
            <button class="VOL" @click="ChangeIndex(1,'VOL')">VOL</button>
        </div>

        <div>
            <button class="S1000009" @click="ChangeSymbol('1000009','1000009')">1000009</button>
            <button class="S1000000" @click="ChangeSymbol('1000000','1000000')">1000000</button>
        </div>
    </div>
</template>

<script>

import $ from 'jquery'
import HQChart from 'hqchart'
import 'hqchart/src/jscommon/umychart.resource/css/tools.css'
import 'hqchart/src/jscommon/umychart.resource/font/iconfont.css'
import io from 'socket.io-client';

function DefaultData() { }

DefaultData.GetKLineOption=function()
{
    //K线配置信息
    var option= 
    {
        Type:'历史K线图',   //创建图形类型
        
        Windows: //窗口指标
        [
            //{Index:"EMPTY", Modify:false,Change:false,TitleHeight:0 },
            {Index:"MA" , Modify:false,Change:false},
            {Index:"VOL", Modify:false,Change:false}, 
            {Index:"RSI", Modify:false,Change:false}, 
        ], 

        //Symbol:'1000000.BIT',   //数字货币使用.BIT后缀
        IsAutoUpdate:false,          //是自动更新数据(不自动更新由外部更新)
        AutoUpdateFrequency:5000,    //数据更新频率
        IsApiPeriod:true,            //使用Api计算周期

        IsShowRightMenu:false,          //右键菜单
        IsClickShowCorssCursor:true,    //手势点击出现十字光标
        //CorssCursorTouchEnd:true,
        StepPixel:5,        //移动一个K线需要的手势移动的像素(默认4)
        ZoomStepPixel:8,    //缩放一次,2个手指需要移动的间距像素(默认5)


        KLine:  //K线设置
        {
            DragMode:1,                 //拖拽模式 0 禁止拖拽 1 数据拖拽 2 区间选择
            Right:0,                    //复权 0 不复权 1 前复权 2 后复权
            Period:0,               //周期 0 日线 1 周线 2 月线 3 年线 
            MaxReqeustDataCount:1000,    //数据个数
            MaxRequestMinuteDayCount:10, //分钟数据取5天
            PageSize:30,                 //一屏显示多少数据
            IsShowTooltip:false,         //是否显示 div K线提示信息
            DrawType:0                   //K线类型 0=实心K线柱子 1=收盘价线 2=美国线 3=空心K线柱子 4=收盘价面积图
        },

        KLineTitle: //标题设置
        {
            IsShowName:false,       //不显示股票名称
            IsShowSettingInfo:true //不显示周期/复权
        },

        Border: //边框
        {
            Left:1,         //左边间距
            Right:60,       //右边间距
            Bottom:25,      //底部间距
            Top:1           //顶部间距
        },
        
        Frame:  //子框架设置
        [
            {SplitCount:3,IsShowLeftText:false, Custom:[{Type:0}] },
            {SplitCount:2,IsShowLeftText:false},
            {SplitCount:2,IsShowLeftText:false}
        ],

        ExtendChart:    //扩展图形
        [
            {Name:'KLineTooltip' }  //手机端tooltip
        ],
    };

    return option;
}

var PERIOD_ID=
[
    {Period:0, Range:86400000 },  //日线
    {Period:1, Range:604800000}, //周线
    {Period:4, Range:60000},       //1分钟
    {Period:5, Range:300000},      //5分钟
    {Period:6,Range:900000},      //15分钟
    {Period:7,Range:1800000},     //30分钟
    {Period:8,Range:3600000},     //60分钟
    {Period:20003,Range:180000},             //3分钟
    {Period:20120,Range:7200000},             //2小时
    {Period:20240,Range:14400000},            //4小时
    {Period:20360,Range:21600000},            //6小时
    {Period:20720,Range:43200000}            //12小时
];


export default 
{
    name:'KLineChart',
    //外部传入的参数
    props: 
    [
        'DefaultSymbol',     //默认股票
    ],

    data()
    {
        let data=
        { 
            Symbol:'1000000',  //货币代码
            Name:'1000000',    //货币名称
            Chart:null,   //图形控件
            Option:DefaultData.GetKLineOption(),   //设置
            ApiUrl:'https://api.ccfox.com/futureQuot/queryCandlestick',    //历史数据请求地址
            WSUrl:'https://futurews.ccfox.com/',
            LatestData:null,  //最后一条数据, 用于tick更新
        }

        return data;
    },

    created:function()
    {
        //处理默认传入的参数
        if (this.DefaultSymbol) this.Symbol=this.DefaultSymbol; //默认股票
    },

    mounted:function()
    {
        console.log(`[KLineChart::mounted]`);

        this.OnSize();              //子组件的mounted在父组件的mounted之前执行了  
        this.CreateKLineChart();  
    },

    methods:
    {
        //////////////////////////////////////////////////////////////////////////////
        //对外接口
        ChangePeriod(period)  //周期切换
        {
            var periodID=this.GetPeriodID(period);
            var symbol=this.Symbol;
            this.LatestData=null;
            this.Chart.ChangePeriod(period);
            if (this.Socket)
            {
                this.Socket.emit('subscribe', { header: { type: 1003},
                    body: 
                    {
                        topics: 
                        [ 
                            { 
                                topic: 'future_kline', 
                                params: 
                                {
                                    symbols: 
                                    [
                                        {
                                            symbol: symbol,
                                            ranges: [periodID.toString()]
                                        }
                                    ]
                                }
                            },
                            {
                                topic: 'future_tick', 
                                params: { symbols: [ {symbol: symbol} ] }
                            }
                        ]
                    }
                });
            }
        },

        ChangeIndex(windowIndex,name) //切换指标  windowIndex=窗口索引, name=指标名字
        {
            this.Chart.ChangeIndex(windowIndex,name);
        },

        ChangeSymbol(symbol,name)   //切换股票
        {
            if (this.Symbol==symbol) return;

            this.Symbol=symbol;
            this.Name=name;
            this.LatestData=null;
            this.Chart.ChangeSymbol(this.Symbol+'.BIT');

            var periodID=this.GetPeriodID(this.Chart.JSChartContainer.Period);
            this.Socket.emit('subscribe', { header: { type: 1003},
                body: 
                {
                    topics: 
                    [ 
                        { 
                            topic: 'future_kline', 
                            params: 
                            {
                                symbols: 
                                [
                                    {
                                        symbol: symbol,
                                        ranges: [periodID.toString()]
                                    }
                                ]
                            }
                        },
                        {
                            topic: 'future_tick', 
                            params: { symbols: [ {symbol: symbol} ] }
                        }
                    ]
                }
            });
        },

        ///////////////////////////////////////////////////////////////////////////////////
        //内部接口
        OnSize()
        { 
            var app=this.$refs.app;
            var height= app.offsetHeight;
            var width = app.offsetWidth;
            console.log(`[KLineChart::OnSize] width=${width} height=${height}`); 

            //获取屏幕大小 动态设置K线的div大小
            var kline=this.$refs.kline;
            kline.style.width=width+'px';
            kline.style.height=(height-90)+'px';

            if (this.Chart) this.Chart.OnSize();
        },

       //创建走势图
        CreateKLineChart()
        {
            if (this.Chart) return;
            this.Option.Symbol=this.Symbol+'.BIT';
            this.Option.NetworkFilter=(data, callback) => { this.NetworkFilter(data, callback); }; //绑定网络协议回调

            //var resource=HQChart.JSChart.GetResource(); //获取全局资源
            //resource.FrameSplitTextFont='30px 微软雅黑';
            let chart=HQChart.Chart.JSChart.Init(this.$refs.kline);
            chart.SetOption(this.Option);
            this.Chart=chart;
            this.CreateWebSocket();
        },

        GetPeriodID(period)
        {
            for(var i in PERIOD_ID)
            {
                if (PERIOD_ID[i].Period==period) return PERIOD_ID[i].Range;
            }
        },

        GetPeriodByID(periodID)
        {
            for(var i in PERIOD_ID)
            {
                if (PERIOD_ID[i].Range==periodID) return PERIOD_ID[i].Period;
            }
        },

        NetworkFilter(data, callback)
        {
            console.log('[KLineChart::NetworkFilter] data', data);
            switch(data.Name)
            {
                case 'KLineChartContainer::ReqeustHistoryMinuteData':   //1分钟全量数据下载
                    this.RequestHistoryMinuteData(data, callback);
                    break;
                case 'KLineChartContainer::RequestHistoryData':         //日线全量数据下载
                    this.RequestHistoryData(data,callback);
                    break;
            }
        },

        RequestHistoryMinuteData(data, callback)  //请求分钟历史数据
        {
            data.PreventDefault=true;
            var self=this;
            var symbol=data.Request.Data.symbol;
            var periodID=this.GetPeriodID(data.Self.Period);
            var url=`${this.ApiUrl}?symbol=${this.Symbol}&range=${periodID}`;
            $.ajax({
                url: url,
                type:"get",
                dataType: "json",
                async:true,
                success: function (recvData)
                {
                    self.RecvHistoryMinuteData(recvData,callback,symbol,self.Name);
                },
                error: function(http,e)
                {
                    self.ChartSplashPaint.IsEnableSplash=false;
                }
            });
            
        },

        RecvHistoryMinuteData(recvData, callback, symbol, name)   //接收历史分钟数据
        {
            var hqChartData={code:0, data:[]};
            hqChartData.symbol=symbol;
            hqChartData.name=name;

            var yClose=null;
            for(var i in recvData.data.lines)
            {
                var item=recvData.data.lines[i];
                var timestamp = item[0];
                var dateTime = new Date();
                dateTime.setTime(timestamp);

                var date=dateTime.getFullYear()*10000+(dateTime.getMonth()+1)*100+dateTime.getDate();
                var time=dateTime.getHours()*100+dateTime.getMinutes();
                var newItem=[date,yClose, parseFloat(item[1]), parseFloat(item[2]), parseFloat(item[3]), parseFloat(item[4]), parseFloat(item[5]), null, time];

                yClose=parseFloat(item[4]);
                hqChartData.data.push(newItem);
            }

            callback(hqChartData);
        },

        RequestHistoryData(data,callback) //日K数据下载
        {
            data.PreventDefault=true;
            var self=this;
            var symbol=data.Request.Data.symbol;
            var periodID=this.GetPeriodID(data.Self.Period);
            var url=`${this.ApiUrl}?symbol=${this.Symbol}&range=${periodID}`;
            $.ajax({
                url: url,
                type:"get",
                dataType: "json",
                async:true,
                success: function (recvData)
                {
                    self.RecvHistoryData(recvData,callback,symbol,self.Name);
                },
                error: function(http,e)
                {
                    self.ChartSplashPaint.IsEnableSplash=false;
                }
            });
        },

        RecvHistoryData(recvData,callback, symbol, name)  //接收到日线数据 转化成hqchart格式数据
        {
            var hqChartData={code:0, data:[]};
            hqChartData.symbol=symbol;
            hqChartData.name=name;

            var yClose=null;
            for(var i in recvData.data.lines)
            {
                var item=recvData.data.lines[i];
                var timestamp = item[0];
                var dateTime = new Date();
                dateTime.setTime(timestamp);

                var date=dateTime.getFullYear()*10000+(dateTime.getMonth()+1)*100+dateTime.getDate();
                var time=dateTime.getHours()*100+dateTime.getMinutes();
                var newItem=[date,yClose, parseFloat(item[1]), parseFloat(item[2]), parseFloat(item[3]), parseFloat(item[4]), parseFloat(item[5]), null];

                yClose=parseFloat(item[4]);
                hqChartData.data.push(newItem);
            }

            callback(hqChartData);
        },

        CreateWebSocket() //创建ws
        {
            var self=this;
            var symbol=this.Symbol;
            var periodID=this.GetPeriodID(this.Option.KLine.Period);
            this.Socket= io(this.WSUrl);
            this.Socket.emit('subscribe', { header: { type: 1003},
                body: 
                {
                    topics: 
                    [ 
                        { 
                            topic: 'future_kline', 
                            params: 
                            {
                                symbols: 
                                [
                                    {
                                        symbol: symbol,
                                        ranges: [periodID.toString()]
                                    }
                                ]
                            }
                        },
                        {
                            topic: 'future_tick', 
                            params: { symbols: [ {symbol: symbol} ] }
                        }
                    ]
                }
            });

            this.Socket.on('future_kline', (data) => { this.RecvWSMessage(data); });
            this.Socket.on('future_tick', function (data) { self.RecvWSTickMessage(data); });
            
            //this.WebSocket.onmessage = function (data) { self.RecvWSMessage(data); }
            //this.WebSocket.onerror = function(data) { self.RecvWSError(data); };
        },

        RecvWSTickMessage(data)
        {
            console.log('[KLineChart::RecvWSTickMessage]', data);
            if (!this.LatestData) return;   //历史数据收完
            if (!data.trades || data.trades.length<=2) return;
            if (data.contractId!=this.Symbol) return;

            var price=parseFloat(data.trades[1]);
            var internalChart=this.Chart.JSChartContainer;
            var period=internalChart.Period;
            console.log(`[KLineChart::RecvWSTickMessage] latest price=${price} , period=${period}`);
            if (period==0 || period==1)
            {
                if (this.LatestData.stock[0].price==price) return;  //价格一样就不用更新
                this.LatestData.stock[0].price=price;
                internalChart.RecvRealtimeData(this.LatestData);
            }
            else
            {
                if (this.LatestData.data[0][5]==price) return   //价格一样就不用更新
                this.LatestData.data[0][5]=price;
                internalChart.RecvMinuteRealtimeData(this.LatestData);
            }
        },

        RecvWSMessage(data)
        {
            console.log('[KLineChart::RecvWSMessage]', data);
            var symbol=data.contractId+'.BIT';
            var periodID=parseInt(data.range);
            var period=this.GetPeriodByID(periodID);
            var internalChart=this.Chart.JSChartContainer;
            if (!internalChart) return;
            if (internalChart.IsEnableSplash) return;   //历史数据还没有下完
            if (symbol!=internalChart.Symbol) return;
            if (period!=internalChart.Period) return;

            if (period==0 || period==1)
            {
                this.UpdateDayData(data,symbol,internalChart);
            }
            else
            {
                this.UpdateMinuteData(data,symbol,internalChart);
            }
        },

        UpdateMinuteData(data, symbol,internalChart)  //分钟K线更新
        {
            var hqChartData={code:0, data:[], ver:2.0 };
            hqChartData.symbol=symbol;
            hqChartData.name=this.Name;

            var yClose=null;
            for(var i in data.lines)
            {
                var item=data.lines[i];
                var timestamp = item[0];
                var dateTime = new Date();
                dateTime.setTime(timestamp);

                var date=dateTime.getFullYear()*10000+(dateTime.getMonth()+1)*100+dateTime.getDate();
                var time=dateTime.getHours()*100+dateTime.getMinutes();
                var newItem=[date,yClose, parseFloat(item[1]), parseFloat(item[2]), parseFloat(item[3]), parseFloat(item[4]), parseFloat(item[5]), null, time];

                yClose=parseFloat(item[4]);
                hqChartData.data.push(newItem);
            }

            internalChart.RecvMinuteRealtimeData(hqChartData);
            if (hqChartData.data.length>0)
                this.LatestData={code:0, data:[hqChartData.data[hqChartData.data.length-1]], ver:2.0}; //保存下 tick更新时用
        },

        UpdateDayData(data, symbol,internalChart) //日线更新
        {
            var stock={symbol:symbol, name:this.Name};
            var hqChartData={code:0, stock:[stock]};

            var tempDate=[];
            var yClose=null;
            for(var i in data.lines)
            {
                var item=data.lines[i];
                var timestamp = item[0];
                var dateTime = new Date();
                dateTime.setTime(timestamp);

                var date=dateTime.getFullYear()*10000+(dateTime.getMonth()+1)*100+dateTime.getDate();
                var time=dateTime.getHours()*100+dateTime.getMinutes();
                var newItem=[date,yClose, parseFloat(item[1]), parseFloat(item[2]), parseFloat(item[3]), parseFloat(item[4]), parseFloat(item[5]), null, time];

                yClose=parseFloat(item[4]);
                tempDate.push(newItem);
            }

            var item=tempDate[tempDate.length-1];   //取最新的一条更新

            stock.date=item[0];
            stock.yclose=newItem[1];
            stock.open=item[2];
            stock.high=item[3];
            stock.low=item[4];
            stock.price=item[5];
            stock.vol=item[6];
            stock.amount=null;

            internalChart.RecvRealtimeData(hqChartData);
            this.LatestData=hqChartData;    //保存下 tick更新时用
        }
    }

}


</script>

<style>
#app
{
    width: 100%;
}
</style>



